.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.moreDropdown{
  color: rgb(110, 110, 110);
  font-size: 12px;
  cursor: pointer;
}

.floatRight{
  float: right;
}

.lr_margin{
  margin: 0px 3% 3% 3%;
  cursor: pointer;
}

.titleImg{
  padding: 1.5%;
  opacity: 0.4;
}

.studioLink:hover{
  cursor: pointer;
}

.ownedBy{
  text-align: right;
}
.zeroBotPadding{
  padding-bottom: 0px;
}

@media (max-width:615px) {
  .loadingAnime{
    width: 84vw!important;
  }
}

.width90per{
  width: 90%;
}

@media (max-width:368px){
  .hideInSmallScreen{
    display: none;
  }
}

.selImgRmv{
  width: 50%;
  margin: 1.5%;
}

.removeStyle{
  background-color: red;
}

.textCntr{
  text-align: center;
}

.txtDivCntr{
      width: 100%;
    margin-top: 10%;
    color: #757575;
}

.bgTrans{
  background: transparent!important;
}

.headerBg{
  background: linear-gradient(180deg, #b5c6e0 0%, #eaeff1 100%);
  padding-bottom: 20px;
}

.bshadow{
  box-shadow: 5px 6px 15px -8px rgba(0,0,0,0.75)!important;
-webkit-box-shadow: 5px 6px 15px -8px rgba(0,0,0,0.75)!important;
-moz-box-shadow: 5px 6px 15px -8px rgba(0,0,0,0.75)!important;
}
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');
.titleTxtColor{
  -webkit-text-fill-color: transparent;
    background: linear-gradient(180deg, #b6c7e0, rgb(0 0 0 /60%));
    -webkit-background-clip: text;
    font-family: Pacifico!important;
}
.btnStyleRefresh{    
  background-color: rgb(0 0 0 / 10%)!important;
    color: #00000080!important;
}

.greyBorderBtn{
  border: 1px solid rgb(180 180 180)!important;
}

.topBarstyle{
    color: #7d8591!important;
}
.titleHeadText{  
  font-family: Kanit;
    }
.topBarstyleIcon{
  color: rgba(0, 0, 0, 0.54);
}

.w50only{
  width: 50%;
}

.viewThumbDiv{
  padding-top: 9px!important;
}

.nextButton{
  text-align: center;
  padding-bottom: 5%;
}

.boldTxt{
  font-weight: 900;
}